













import { Vue, Component } from "vue-property-decorator";

@Component({})
export default class Notelist extends Vue {
  tableData = [];
  fileList = [];
  formLabelAlign: any = {
    name: "",
    context: {}
  };
  async fetch() {
    const res = await this.$http.get("Note");
    this.tableData = res.data;
  }
  created() {
    this.fetch();
  }
}
